.newsletter {
  background-color: #1e96ba;
}
.newsletter__left {
  margin-right: 10rem;
}
@media only screen and (max-width: 75em) {
  .newsletter__left {
    margin-right: 0;
    text-align: center;
    max-width: 49rem;
  }
}
.newsletter__box {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}
@media only screen and (max-width: 75em) {
  .newsletter__box {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
.newsletter__right {
  padding: 3rem 0 1rem 0;
  border-bottom: 2px solid #2c2c2c;
}
@media only screen and (max-width: 75em) {
  .newsletter__right {
    margin-top: 2rem;
    max-width: 40rem;
  }
}
.newsletter-input {
  margin-right: 1rem;
  padding: 1.5rem 0 1.5rem 0;
  border: none;
  font-weight: 600;
  display: inline !important;
  width: 100%;
  background-color: transparent;
}
.newsletter-input:focus {
  outline: none;
}
.newsletter-input::placeholder {
  color: #111111;
  opacity: 1;
  font-size: 1.6rem !important;
  font-weight: 600;
}